.bio {
    padding-top: 3%;
    padding-bottom: 10%;
    background-color: #DCDFE5;
}

.about-me {
    font-weight: normal;
    font-size: 4rem;
    color: #565D6D;
    margin-left: 10%;
    border: solid #565D6D;
    border-width: 0 0 1px 0;
}

@media screen and (min-width: 901px) {
    .bio-content {
        margin-left: 45%;
        color: #565D6D;
        font-size: 1.3em;
    }
    
    .trees {
        position: absolute;
        width: 25%;
        opacity: 0.4;
        margin-left: 15%;
        margin-top: 6%;
    }
}

@media screen and (min-width: 751px) and (max-width: 900px) {
    .bio-content {
        margin-left: 35%;
        color: #565D6D;
        font-size: 1em;
    }
    
    .trees {
        position: absolute;
        width: 30%;
        opacity: 0.2;
        margin-left: 5%;
        margin-top: 10%;
    }
}

@media screen and (min-width: 601px) and (max-width: 750px) {
    .bio-content {
        margin-left: 15%;
        color: #565D6D;
        font-size: 1em;
    }
    
    .trees {
        position: absolute;
        width: 40%;
        opacity: 0.15;
        margin-left: 50%;
        margin-top: 6%;
    }
}

@media screen and (min-width: 451px) and (max-width: 600px) {
    .about-me {
        font-size: 3em;
    }
    .bio-content {
        margin: 0 auto;
        color: #565D6D;
        font-size: 1em;
    }
    
    .trees {
        position: absolute;
        width: 50%;
        opacity: 0.15;
        margin-left: 25%;
        margin-top: 2%;
    }
}

@media screen and (max-width: 450px) {
    .about-me {
        font-size: 2em;
    }
    .bio-content {
        margin: 0% auto;
        color: #565D6D;
        font-size: 1em;
    }
    
    .trees {
        position: absolute;
        width: 60%;
        opacity: 0.15;
        margin-left: 20%;
        margin-top: 15%;
    }
}