.projects {
    padding-top: 3%;
    padding-bottom: 8%;
    background-color: #565D6D;
}

.my-projects {
    font-weight: normal;
    font-size: 4rem;
    text-align: right;
    color: #DCDFE5;
    margin-right: 10%;
    border: solid #DCDFE5;
    border-width: 0 0 1px 0;
}

.dots {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.project-dot-active, .project-dot-inactive {
    border: 2px solid;
    text-align: center;
    margin: 5px;
    padding: 5px;
    border-radius: 20px;
    width: 16px;
}

.project-dot-active {
    border-color: #DCDFE5;
    color: #DCDFE5;
}

.project-dot-inactive {
    border-color: #9199AD;
    color: #9199AD;
    transition: 0.2s ease-in-out;
}

.project-dot-inactive:hover {
    border-color: #DCDFE5;
}

.project-content {
    padding-bottom: 3%;
}

.project-title {
    text-align: center;
    font-weight: normal;
    color: #DCDFE5;
}

.project-description {
    color: #9199AD;
    font-size: 1.2em;
}

@media screen and (min-width: 901px) {
    .projects-content {
        margin-left: 5%;
    }
    .mountains {
        position: absolute;
        width: 65%;
        opacity: 0.2;
        margin-left: 32%;
        margin-top: 27%;
    }
}

@media screen and (min-width: 751px) and (max-width: 900px) {
    .projects-content {
        margin-left: 5%;
    }
    .mountains {
        position: absolute;
        width: 70%;
        opacity: 0.2;
        margin-left: 20%;
        margin-top: 40%;
    }
}

@media screen and (min-width: 601px) and (max-width: 750px) {
    .projects-content {
        margin: 0 auto;
    }
    .mountains {
        position: absolute;
        width: 70%;
        opacity: 0.2;
        margin-left: 15%;
        margin-top: 20%;
    }
}

@media screen and (min-width: 451px) and (max-width: 600px) {
    .my-projects {
        font-size: 3em;
    }
    .projects-content {
        margin: 0 auto;
    }
    .mountains {
        position: absolute;
        width: 80%;
        opacity: 0.2;
        margin-left: 10%;
        margin-top: 27%;
    }
}

@media screen and (max-width: 450px) {
    .my-projects {
        font-size: 2em;
    }
    .projects-content {
        margin: 0 auto;
    }
    .mountains {
        position: absolute;
        width: 90%;
        opacity: 0.2;
        margin-left: 5%;
        margin-top: 30%;
    }
}