.contact {
    padding-top: 3%;
    padding-bottom: 10%;
    background-color: #DCDFE5;
}

.contact-me {
    font-weight: normal;
    font-size: 4rem;
    color: #565D6D;
    margin-left: 10%;
    border: solid #565D6D;
    border-width: 0 0 1px 0;
}

@media screen and (min-width: 451px) and (max-width: 600px) {
    .contact-me {
        font-size: 3em;
    }
}

@media screen and (max-width: 450px) {
    .contact-me {
        font-size: 2em;
    }
}

.contact-links {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.contact-link {
    border-radius: 50%;
    color: #565D6D;
    margin: 10px;
    width: 250px;
    height: 250px;
    text-align: center;
    transition: 0.3s ease-in-out;
    overflow: hidden;
    position: relative;
}

.contact-link:before, .contact-link:after {
    position: absolute;
    content: '';
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    transition: 0.3s ease-in-out;
}

.contact-link:before {
    width: 75%;
    height: 75%;
    border: solid #565D6D;
    border-width: 0 5px 0 5px;
}

.contact-link:after {
    width: 85%;
    height: 85%;
    border: solid #565D6D;
    border-width: 5px 0 5px 0;
}

.contact-link:hover:before {
    width: 85%;
    height: 85%;
}

.contact-link:hover:after {
    width: 75%;
    height: 75%;
}

.contact-link-text {
    font-size: 1.2em;
    margin: 55px;
}

@keyframes rotation1 {
    0%{
        transform: translate(-50%, -50%) rotate(0deg);
        width: 75%;
        height: 75%;
    }
    50%{
        transform: translate(-50%, -50%) rotate(180deg);
        width: 85%;
        height: 85%;
    }
    100%{
        transform: translate(-50%, -50%) rotate(360deg);
        width: 75%;
        height: 75%;
    }
}

@keyframes rotation2 {
    0%{
        transform: translate(-50%, -50%) rotate(0deg);
        width: 85%;
        height: 85%;
    }
    50%{
        transform: translate(-50%, -50%) rotate(-180deg);
        width: 75%;
        height: 75%;
    }
    100%{
        transform: translate(-50%, -50%) rotate(-360deg);
        width: 85%;
        height: 85%;
    }
}